import React, { useState } from 'react'
import { groupID } from '../../../settings/config';
import swal from 'sweetalert';
import { qlNguoiDungService } from '../../../services/quanLyNguoiDungService'
import { NavLink } from 'react-router-dom';

export default function AddUser() {

    let [dataThemUser, setDataThemUser] = useState({
        values: {
            taiKhoan: "",
            matKhau: "",
            email: "",
            soDt: "",
            maLoaiNguoiDung: "KhachHang",
            hoTen: ""
        },
        errors: {
            taiKhoan: "",
            matKhau: "",
            email: "",
            soDt: "",
            maLoaiNguoiDung: "",
            hoTen: ""
        }
    })

    const handleChange = (event) => {
        let { value, name } = event.target;

        const newValues = {
            ...dataThemUser.values,
            [name] : value
        }
        const newErrors = {
            ...dataThemUser.errors,
        }
        if (name === 'taiKhoan') {
            let regexUsername = /^[a-zA-Z0-9.\-_$@*!]{8,20}$/;
            if (value.trim() === '') {
                newErrors.taiKhoan = 'Khng c b trng';
            }
            else if (!value.match(regexUsername)) {
                newErrors.taiKhoan = 'Tn ti khon gm cc k t vit lin khng du, t 8-20 k t'
            }
            else {
                newErrors.taiKhoan = ''
            }
        }
        if (name === 'matKhau') {
            let regexPassword = /^[a-zA-Z0-9.\-_$@*!]{9,20}$/;
            if (value.trim() === '') {
                newErrors.matKhau = 'Khng c b trng';
            }
            else if (!value.match(regexPassword)) {
                newErrors.matKhau = 'Mt gm cc k t vit lin khng du, t 9-20 k t'
            }
            else {
                newErrors.matKhau = ''
            }
        }
        if (name === 'hoTen') {
            if (value.trim() === '') {
                newErrors.hoTen = 'Khng c b trng';
            }
        }
        if (name === "soDt") {
            let regexNumber = /^[0-9]+$/;
            if (value.trim() === '') {
                newErrors.soDt = 'Khng c b trng';
            }
            else if (!value.match(regexNumber)) {
                newErrors.soDt = 'S in thoi khng hp l';
            }
            else {
                newErrors.soDt = ''
            }
        }
        if (name === "email") {
            let regexEmail = /^\w+@[a-zA-Z_]+?\.[a-zA-Z]{2,3}$/;
            if (value.trim() === '') {
                newErrors.email = 'Khng c b trng';
            }
            else if (!value.match(regexEmail)) {
                newErrors.email = 'Email khng hp l';
            }
            else {
                newErrors.email = ''
            }
        }
        setDataThemUser({ values: newValues, errors: newErrors });
    }

    const handleSubmit = (event) => {
        event.preventDefault();
        let { values, errors } = dataThemUser;
        let valid = true;
        for (let key in values) {
                if (values[key] === '') { //Nu nh c 1 values = rng th khng hp l
                    valid = false;
            }
        }
        //check errors
        for (let key in errors) {
            if (errors[key] !== '') { //Nu nh c 1 errrors != rng => cn li
                valid = false;
            }
        }
        if (!valid) {
            swal("Oops", "Bn cha in y  thng tin hoc c thng tin cha hp l!", "error", {
                buttons: false,
                timer: 2000,
            });
            return;
        }
        console.log('succcess')
        qlNguoiDungService.themNguoiDung(values).then(res => {
            // console.log(res.data);
            swal('', "Thm ngi thnh cng", "success", {
                buttons: false,
                timer: 1500,
            });
        }).catch(err => {
            // console.log(err.response.data);
            swal('Oops', err.response.data, "error", {
                buttons: false,
                timer: 1500,
            });
        })
    }


    return (
        <form onSubmit={handleSubmit} className="container">
            <h3 className="text-center">
                THM NGI DNG
                    </h3>
            <div className="row">
                <div className="col-6">
                    <div className="form-group">
                        <label>Ti khon *</label>
                        <input name="taiKhoan" className="form-control" onChange={handleChange} />
                        <span className="text-danger">{dataThemUser.errors.taiKhoan}</span>
                    </div>
                    <div className="form-group">
                        <label>Mt khu *</label>
                        <input type="password" name="matKhau" className="form-control" onChange={handleChange} />
                        <span className="text-danger">{dataThemUser.errors.matKhau}</span>
                    </div>
                    <div className="form-group">
                        <label>H Tn *</label>
                        <input name="hoTen" className="form-control" onChange={handleChange} />
                        <span className="text-danger">{dataThemUser.errors.hoTen}</span>
                    </div>
                </div>
                <div className="col-6">
                    <div className="form-group">
                        <label>Email *</label>
                        <input name="email" className="form-control" onChange={handleChange} />
                        <span className="text-danger">{dataThemUser.errors.email}</span>
                    </div>
                    <div className="form-group">
                        <label>S in thoi *</label>
                        <input name="soDt" className="form-control" onChange={handleChange} />
                        <span className="text-danger">{dataThemUser.errors.soDt}</span>
                    </div>
                    <div className="form-group">
                        <label>Loi ngi dng *</label>
                        <select className="form-control" name="maLoaiNguoiDung" onChange={handleChange}>
                            <option value="KhachHang">Khch hng</option>
                            <option value="QuanTri">Qun tr</option>
                        </select>
                    </div>
                </div>
                <p style={{paddingLeft: '15px'}}>*: Bt buc</p>
            </div>
            <div className="clearfix">
                <div className="float-left">
                <NavLink className="form-control btn btn-primary" to="/admin/quanlynguoidung">Tr v</NavLink>
                </div>
                <div className="float-right">
                <button type="submit" className="form-control btn btn-success" style={{width: '150px'}}>Thm</button>
                </div>
            
            
            </div>
        </form>
    )
}
