import React, { useState } from 'react'
import { NavLink } from 'react-router-dom';
import { groupID } from '../../settings/config';
import { qlNguoiDungService } from '../../services/quanLyNguoiDungService'
import swal from 'sweetalert';

export default function Register(props) {

    let [state, setState] = useState({
        values: {
            taiKhoan: '',
            matKhau: '',
            email: '',
            soDt: '',
            maNhom: groupID,
            maLoaiNguoiDung: 'KhachHang',
            hoTen: '',
        },
        errors: {
            taiKhoan: '',
            matKhau: '',
            email: '',
            soDt: '',
            hoTen: '',
        }
    });

    const handleChangeInput = (event) => {
        let { value, name } = event.target;
        //To ra object this.state.values mi
        const newValues = {
            ...state.values,
            [name]: value
        }
        const newErrors = { ...state.errors };
        // console.log(newErrors)
        if (name === 'taiKhoan') {
            let regexUsername = /^[a-zA-Z0-9.\-_$@*!]{8,20}$/;
            if (value.trim() === '') {
                newErrors.taiKhoan = 'Khng c b trng';
            }
            else if (!value.match(regexUsername)) {
                newErrors.taiKhoan = 'Tn ti khon gm cc k t vit lin khng du, t 8-20 k t'
            }
            else {
                newErrors.taiKhoan = ''
            }
        }
        if (name === 'matKhau') {
            let regexPassword = /^[a-zA-Z0-9.\-_$@*!]{9,20}$/;
            if (value.trim() === '') {
                newErrors.matKhau = 'Khng c b trng';
            }
            else if (!value.match(regexPassword)) {
                newErrors.matKhau = 'Mt gm cc k t vit lin khng du, t 9-20 k t'
            }
            else {
                newErrors.matKhau = ''
            }
        }
        if (name === 'hoTen') {
            if (value.trim() === '') {
                newErrors.hoTen = 'Khng c b trng';
            }
        }
        if (name === "soDt") {
            let regexNumber = /^[0-9]+$/;
            if (value.trim() === '') {
                newErrors.soDt = 'Khng c b trng';
            }
            else if (!value.match(regexNumber)) {
                newErrors.soDt = 'S in thoi khng hp l';
            }
            else {
                newErrors.soDt = ''
            }
        }
        if (name === "email") {
            let regexEmail = /^\w+@[a-zA-Z_]+?\.[a-zA-Z]{2,3}$/;
            if (value.trim() === '') {
                newErrors.email = 'Khng c b trng';
            }
            else if (!value.match(regexEmail)) {
                newErrors.email = 'Email khng hp l';
            }
            else {
                newErrors.email = ''
            }
        }
        // console.log(newErrors)
        //setState li values v errors
        setState({ values: newValues, errors: newErrors });
    };

    const handleSubmit = (event) => {
        //Chn s kin load li trang t form submit
        event.preventDefault();
        let { values, errors } = state;
        let valid = true;
        for (let key in values) {
            if (values[key] === '') { //Nu nh c 1 values = rng th khng hp l
                valid = false;
            }
        }
        //check errors
        for (let key in errors) {
            if (errors[key] !== '') { //Nu nh c 1 errrors != rng => cn li
                valid = false;
            }
        }
        if (!valid) {
            swal('Oops', 'Thng tin khng hp l!', "error", {
                buttons: false,
                timer: 1500,
            });
            return;
        }
        //Gi api hoc dispatch redux
        qlNguoiDungService.dangKy(state.values).then(res => {
            // console.log(res.data);
            swal('', "ng k thnh cng", "success", {
                buttons: false,
                timer: 1500,
            });
            props.history.goBack();
        }).catch(err => {
            // console.log(err.response.data);
            swal('Oops', err.response.data, "error", {
                buttons: false,
                timer: 1500,
            });
        })
    }



    return (
        <form className="container" onSubmit={handleSubmit} className="register" style={{ width: '500px' }}>
            <a className="navbar-brand" href="#"><img src={"./images/cybersoftlogo.png"} style={{ width: 45, height: 45 }} /></a>
            <h4>ng k</h4>
            <div className="row">
                <div className="form-group col-6">
                    <span>H tn</span>
                    <input className="form-control" name="hoTen" onChange={handleChangeInput} />
                    <span className="text-danger">{state.errors.hoTen}</span>
                </div>
                <div className="form-group col-6">
                    <span>S in thoi</span>
                    <input className="form-control" name="soDt" onChange={handleChangeInput} />
                    <span className="text-danger">{state.errors.soDt}</span>

                </div>

            </div>
            <div className="row">
            <div className="form-group col-6">
                    <span>Ti khon</span>
                    <input className="form-control" name="taiKhoan" onChange={handleChangeInput} />
                    <span className="text-danger">{state.errors.taiKhoan}</span>
                </div>
                
                <div className="form-group col-6">
                    <span>Email</span>
                    <input className="form-control" name="email" onChange={handleChangeInput} />
                    <span className="text-danger">{state.errors.email}</span>

                </div>
            </div>
            <div className="row">
            <div className="form-group col-6">
                    <span>Mt khu</span>
                    <input type="password" className="form-control" name="matKhau" onChange={handleChangeInput} />
                    <span className="text-danger">{state.errors.matKhau}</span>
                </div>
                
                <div className="form-group btnSubmit col-6">
                    <button type="submit" className="btn btn-success">ng k</button>
                </div>

            </div>
            <div className="directToLogin">
                <span> c ti khon?</span>
                <NavLink to="/login"> ng nhp ngay!</NavLink>
            </div>


        </form>
    )
}
